html, *, :before, :after {
  box-sizing: border-box;
}

body {
  color: var(--main-text-color);
  letter-spacing: var(--main-letter-spacing);
  background-color: #fff;
  font-family: Roboto, "sans-serif";
  font-size: 14px;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (max-width: 479px) {
  .container {
    width: 100%;
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1249px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1250px) {
  .container {
    width: 1250px;
  }
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

picture {
  margin: 0;
  padding: 0;
  display: block;
}

:root {
  --focus-pinc-color: #d41443;
  --accent-salmon-color: #ffa5ba;
  --text-black-color: black;
  --text-gray-color: #907e82;
  --focus-white-color: #fff;
  --main-transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

